

const env = {
  API_URL: process.env.API_URL ? process.env.API_URL : 'http://www.parrot-corporate.local/',
  API_BASE: process.env.API_BASE ? process.env.API_BASE : 'jsonapi',
  BASIC_AUTH_USERNAME: process.env.BASIC_AUTH_USERNAME ? process.env.BASIC_AUTH_USERNAME : 'admin',
  BASIC_AUTH_PASSWORD: process.env.BASIC_AUTH_PASSWORD ? process.env.BASIC_AUTH_PASSWORD : '123456',
  API_TYPE: process.env.API_TYPE ? process.env.API_TYPE : "?_format=json"
};

function config (lang = 'en', url, jsonapi = false, rest = false) {
  return {
    method: 'get',
    //mode: 'cors',
    baseURL: env.API_URL + lang + '/' + (!rest ? env.API_BASE : ''),
    url: url + (jsonapi ? env.API_TYPE : ''),
    headers: {
      'Content-Type': 'application/json',
      //'Authorization': 'Bearer '+ Buffer.from(env.BASIC_AUTH_USERNAME + ":" + env.BASIC_AUTH_PASSWORD).toString('base64'),
      'Accept-Language': lang
    },
    auth: {
      username: env.BASIC_AUTH_USERNAME,
      password: env.BASIC_AUTH_PASSWORD
    }
    //responseType: 'json'
  }
};



module.exports.env = env;
module.exports.config = config;