import validator from 'validator';
import React from 'react';
import StringHandler from './string-handler';

export default class FormValidation {
    /**
     * Check Input Require
     * @param value
     * @returns {string}
     */
    static required(value) {
        if (!value.toString().trim().length) {
            // We can return string or jsx as the 'error' prop for the validated Component
            return 'require';
        }
    };

    /**
     * Check Input is Email
     * @param value
     * @returns {string}
     */
    static  email (value) {
        if (!validator.isEmail(value)) {
            return `${value} is not a valid email.`
        }
    };

    /**
     * Check Input Length Validation
     * @param value
     * @param props
     * @returns {*}
     */
    static  lt (value, props) {
        // get the maxLength from component's props
        if (!value.toString().trim().length > props.maxLength) {
            // Return jsx
            return <span className="error">The value exceeded {props.maxLength} symbols.</span>
        }
    };

    static validationMessage(type, lang) {
        let string = "";
        switch (type) {
            case 'required':
                string = StringHandler.stringI18n("This field is required", lang);
                break;
            case 'email':
                string = StringHandler.stringI18n("Email format not valid", lang);
                break;
        }
        return string;
    }
}