const request_config = require('./request-config');
const axios = require('axios');

function form_api(lang = 'en', form, data) {
  const config = request_config.config(lang, '/entity/'+form + '_message/', true, true);
  config.method = 'POST';
  config.data = data;
  return axios.request(config);
}
  
module.exports = form_api;