import React from 'react'

import Layout from '../components/layout/layout';
import PageContent from '../components/layout/pageContent';
import {Container, Row, Col} from 'reactstrap';
import ContactForm from '../components/form/ContactForm';

const ContactPage = ({pageContext}) => {
    return (
        <Layout context={pageContext}>
            <PageContent context={pageContext}/>
            <Container>
                <Row className="contact">
                    <Col lg="6" md="6" xs="12" className="information">
                        <div className="content" dangerouslySetInnerHTML={{__html: pageContext.node_content.field_contact_info[0].value}}/>
                    </Col>
                    <Col lg="6" md="6" xs="12" className="form">
                        <ContactForm lang={pageContext.lang} />
                    </Col>
                </Row>
            </Container>
        </Layout>
    )
};

export default ContactPage
